import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { EditSubTypeOfHousingComponent } from './edit-sub-type-of-housing.component';
const routes: Routes = [
  {
    path: '',
    component: EditSubTypeOfHousingComponent,
    data: {
      title: 'Edit Sub Type of Housing'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EditSubTypeOfHousingRoutingModule {}



