import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, BooksApi, ContainerApi, Sub_type_of_housing_masterApi } from '../../../../shared/loopback-sdk';
import * as moment from 'moment'; //for date manupalation
import { CookieService } from 'angular2-cookie/core';
import { Headers, Http, Response } from '@angular/http';
import { stringify } from 'querystring';
import { ShareService } from '../../../../providers/share-service/share-service';
declare var window: any;
declare var hostname: any;
declare var sdkPrefix;
@Component({
	templateUrl: 'edit-sub-type-of-housing.component.html'
})
export class EditSubTypeOfHousingComponent {
	@ViewChild('name') name: NgModel;
	@ViewChild('priority') priority: NgModel;
	public id: number;
	display: boolean = false; result: any;
	submitted: boolean = false;
	subTypOfHusngName: any;
	typeOfHousingName:any;
	busy: any;
	userDetails: any;
	rolename: any;
	loading: boolean = false;
	subTypeOfHousngStatus:boolean = false;
	bookImage:any;
	loginFlagToken:any;
	projectCount : number = 0;
	municipalityCount : number = 0;
	currentTime:string;
	editSubTypOfHousing={
		"id":0,
		"name":"",
		"type_of_housing":"",
		"parent_id":0,
		//"created_at":"",
		//"updated_at":"",
		"status": 0
	}
	constructor(private loopAuth: LoopBackAuth, private route: ActivatedRoute, private http: Http, private _cookieService: CookieService, private books: BooksApi, private router: Router, private containerApi: ContainerApi, private share: ShareService,  private subTypHousngApi:Sub_type_of_housing_masterApi) {
		this.userDetails = this.loopAuth.getCurrentUserData();
		this.rolename = this.userDetails.userDet.roles;
		this.id = this.route.snapshot.params['id'];
		this.loginFlagToken = this._cookieService.get(sdkPrefix+'id');
	}

	ngOnInit(): void {
		this.loading = true;
		var dataArrObj = {
			"id":this.id
		}
		var date = new Date();
		this.currentTime = moment().format("YYYY-MM-DD H:mm:s");
		console.log("time ",this.currentTime);
		this.subTypHousngApi.getSubTypOfHousing(dataArrObj).subscribe(data=>{
			this.loading = false;
			if(data){
				let result = data.result[0];
				this.subTypOfHusngName = result.name;
				this.subTypeOfHousngStatus = (result.status)?true:false;
				this.projectCount = result.usedProjects;
				this.typeOfHousingName = result.type_of_housing;
				console.log("projectCount ",this.projectCount);
				this.editSubTypOfHousing={
					"id" : this.id,
					"name": result.name,
					"type_of_housing":result.type_of_housing,
					"parent_id":result.parent_id,
					"status": result.status
					//"created_at":result.created_at,
					//"updated_at":result.updated_at,
				}

			}
		},
		err=>{
			console.log("err", err);
			this.loading = false;
		},
		()=>{

		});
	}

	goToListing() {
		// window.location.reload();
		this.router.navigate(["sub-type-of-housing"]);
	}

	async submit(provinceForm) {
		this.submitted = true;
		if (provinceForm.valid) {
			this.loading = true;
			
			this.editSubTypOfHousing.id = this.id;
			this.editSubTypOfHousing.name = this.capital_letter(this.subTypOfHusngName);
			this.editSubTypOfHousing.status = (this.subTypeOfHousngStatus)?1:0;
			//this.editSubTypOfHousing.updated_at = this.currentTime.toString();

			this.checkCount(this.editSubTypOfHousing).then((total)=>{
				if(total > 0 && !this.editSubTypOfHousing.status){
					this.loading = false;
					this.result = "Sub Type of housing is already been used so it cannot be inactive";
					this.display = true;
				}else{
			
					this.subTypHousngApi.upsertPatch(this.editSubTypOfHousing).subscribe((data)=>{
						this.loading = false;
						this.result = "success";
						this.display = true;

					},(err)=>{
						this.loading = false;
						this.result = "Something went wrong";
						this.display = true;
					});
				}
			},(err)=>{
				this.loading = false;
				this.result = "Something went wrong";
				this.display = true;
			});

			
		}
	}

	ifNameExist(dataObj){
		return new Promise((resolve,reject)=>{
			try{
				let access_token = this.loopAuth.getAccessTokenId();
				let api="/api/type_of_housing_masters/count?where=";
				let cond = { "name": dataObj.name};
				let url = hostname+api+encodeURI(JSON.stringify(cond))+"&access_token="+access_token;
				this.http.get(url).subscribe((data)=>{
					console.log("Count ",data.json());
					resolve(data.json().count);
				},(err)=>{
					reject(err);
				});

			}catch(err){
				reject(err);
			}
		});
		
		
	}
	checkCount(dataObj){
		return new Promise((resolve,reject)=>{
			try{
				let access_token = this.loopAuth.getAccessTokenId();
				let sub_type_of_housing_id = dataObj.id;
				let cond = {"sub_type_of_housing_id": sub_type_of_housing_id};
				
				let api="/api/type_of_housing_mappings/count?where=";
				
				let url = hostname+api+encodeURI(JSON.stringify(cond))+"&access_token="+access_token;
				this.http.get(url).subscribe((data)=>{
					//console.log("Count ",data.json());
					resolve(data.json().count);
				},(err)=>{
					reject(err);
				});
			}
			catch(err){
				reject(true);
			}
		});
	}

	capital_letter(str) 
	{
		if(str != '' && str != null && str != undefined){
			str = str.split(" ");

			for (var i = 0, x = str.length; i < x; i++) {
				str[i] = str[i][0].toUpperCase() + str[i].substr(1);
			}
	
			return str.join(" ");
		}else{
			return str
		}
		
	}


	
}


